.container {
    align-content: center;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.top-row, .bottom-row {
    display: flex;

    width: 100%;
}

.square {
    flex: 1;
    padding: 10px;
    background-color: #ccc;
    margin: 5px;
}

